*,
*::after,
*::before {
	box-sizing: border-box;
}

:root {
	font-size: 16px;
}

body {
	margin: 0;
	--color-text: rgb(255, 254, 252);
	--color-bg: rgb(15 16 17);
	--color-link: rgb(255, 254, 252);
	--color-link-hover: white;
	--color-gradient: linear-gradient(45deg, rgb(51 102 255), rgb(51 153 255) 24%, rgb(207 158 250));
	color: var(--color-text);
	background-color: var(--color-bg);
	font-family: SF Pro Text,-apple-system,BlinkMacSystemFont,Segoe UI,Helvetica Neue,Helvetica,Arial,sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	--cursor-stroke: white; 
	--cursor-fill: none; 
	--cursor-stroke-width: 1px; 
	line-height: 1;
	font-weight: 400;
	overflow: hidden;
	overflow-y: scroll;
}

.off__canvas {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: var(--color-bg);
	display: flex;
	flex-direction: column;
    height: auto;
    max-height: calc(100vh - 4rem - env(safe-area-inset-bottom));
    overflow-y: auto;
    margin: 0;
    z-index: 1502;
    -webkit-transition: all 0.4s ease-out;
    transition: all 0.4s ease-out;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d( 0, -100%, 0);
}

.active .off__canvas {
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

.off__canvas button.close__off-canvas {
	margin: auto;
}
  
.active::after {
	content: "";
	display: block;
	position: fixed;
	z-index: 1501;
	height: 100%;
	overflow: hidden;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0; 
	background: rgba(15,16,17,0.5);
}

.canvas__bottom {
    position: sticky;
    bottom: 0;
    align-self: auto;
	margin-top: 2.5rem;
    padding-bottom: 4rem;
    z-index: 1503;
    background: linear-gradient(to bottom, rgb(15 16 17 / 0%) 0%, rgb(15 16 17) 100%);
}

.off__canvas .about__text {
	margin-top: 4rem;
}

.about__text {
	max-width: 720px;
    margin: auto;
    padding: 0 1rem;
}

.active body,
.oh {
	overflow: hidden;
}

/* Page Loader */
.js .loading::before,
.js .loading::after {
	content: '';
	position: fixed;
	z-index: 5000;
}

.js .loading::before {
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: var(--color-bg);
}

.js .loading::after {
	top: 50%;
	left: 50%;
	width: 60px;
	height: 60px;
	margin: -30px 0 0 -30px;
	border-radius: 50%;
	opacity: 0.4;
	background: var(--color-gradient);
	animation: loaderAnim 0.7s linear infinite alternate forwards;

}

@keyframes loaderAnim {
	to {
		opacity: 1;
		transform: scale3d(0.5,0.5,1);
	}
}

::-moz-selection { /* Code for Firefox */
	background: rgb(0 186 226 / 50%);
  }
  
::selection {
	background: rgb(0 186 226 / 50%);
}

a {
	text-decoration: none;
	color: var(--color-link);
	outline: none;
}

a:hover {
	color: var(--color-link-hover);
	outline: none;
}

/* Better focus styles from https://developer.mozilla.org/en-US/docs/Web/CSS/:focus-visible */
a:focus {
	/* Provide a fallback style for browsers
	 that don't support :focus-visible */
	outline: none;
	background: lightgrey;
}

a:focus:not(:focus-visible) {
	/* Remove the focus indicator on mouse-focus for browsers
	 that do support :focus-visible */
	background: transparent;
}

a:focus-visible {
	/* Draw a very noticeable focus style for
	 keyboard-focus on browsers that do support
	 :focus-visible */
	outline: 2px solid rgb(98, 25, 255);
	background: transparent;
}

.unbutton {
	background: none;
	border: 0;
	padding: 0;
	margin: 0;
	font: inherit;
}

.unbutton:focus {
	outline: none;
}

main {
	padding: 0;
}

.cursor {
	display: none;
}

.button {
    pointer-events: auto;
    cursor: pointer;
    background: transparent;
    border-radius: 1rem;
    padding: 0.625rem 0.75rem;
	margin: 0;
    border: none;
    font-size: 0.75rem;
	line-height: 1;
	white-space: nowrap;
    position: relative;
    display: block;
	color: var(--color-link);
}

.button::before,
.button::after {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.button.accent {
	background: rgb(62 64 66);
	transition: 0.2s;
}

.button.accent:hover {
	color: rgb(15 16 17);
	background-color: white;

}

.button.primary {
    color: white;
	font-weight: 700;
}

.button--this > span {
	display: inline-block;
}

.button--this:hover > span {
	opacity: 0;
}

.marquee {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	overflow: hidden;
	pointer-events: none;
}

.marquee__inner {
	width: fit-content;
	display: flex;
	position: relative;
	--offset: 1rem;
	--move-initial: calc(-25% + var(--offset));
	--move-final: calc(-50% + var(--offset));
	transform: translate3d(var(--move-initial), 0, 0);
	animation: marquee 1s linear infinite;
	animation-play-state: paused;
	opacity: 0;
}

.button--this:hover .marquee__inner {
	animation-play-state: running;
	opacity: 1;
	transition-duration: 0.4s;
}

.marquee span {
    text-align: center;
    white-space: nowrap;
    font-style: italic;
    padding: 0.625rem 1rem;
}

.link {
    cursor: pointer;
    font-size: 18px;
    position: relative;
    white-space: nowrap;
    color: var(--color-text);
}

/* .link::before, */
.link::after {
    position: absolute;
    width: 100%;
    height: 1px;
    background: currentColor;
    top: 100%;
    left: 0;
    pointer-events: none;
}

@keyframes marquee {
	0% {
		transform: translate3d(var(--move-initial), 0, 0);
	}

	100% {
		transform: translate3d(var(--move-final), 0, 0);
	}
}

.link__graphic {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
    fill: none;
    stroke: var(--color-link);
    stroke-width: 1px;
}

.link__graphic--stroke path {
    stroke-dasharray: 200;
    stroke-dashoffset: 200;
}

.link:hover .link__graphic--stroke path {
    stroke-dashoffset: 0;
}

.link__graphic--scribble {
    top: 1.25rem
}

.link__graphic--scribble path {
    transition: stroke-dashoffset 0.6s cubic-bezier(0.7, 0, 0.3, 1);
}

.link:hover .link__graphic--scribble path {
    transition-timing-function: cubic-bezier(0.8, 1, 0.7, 1);
    transition-duration: 0.3s;
}

.link--scribble {
	font-size: 0.875rem;
}

.link--scribble::before {
    display: none;
}

.mono {
    font-family: Space Mono, monospace;
}

h1, h2, h3, h4, h5, h6, p {
	line-height: 1.6;
	font-weight: 400;
	margin: 0;
	margin-bottom: 0.5rem;
}

h4 {
    font-size: 1.125rem;
	line-height: 1.2;
	font-weight: 500;
}

h5 {
    font-size: 1rem;
	line-height: 1.2;
}

ul {
    list-style: none;
    display: flex;
    flex-flow: column wrap;
    align-items: flex-start;
    row-gap: .5rem;
    padding: 0;
    margin: 0;
}

.gradient {
	background-color: rgb(221, 214, 243);
	background-image: var(--color-gradient);	
}

.site__header {
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 4rem;
	padding: 0 1rem 0 .5rem;
    z-index: 1500;
    -webkit-backdrop-filter: blur(16px);
    backdrop-filter: blur(16px);
    background-color: rgb(15 16 17 / 0%);
    inset: 0% 0% auto;
}

.header__item {
	display: flex;
}

.site-title {
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
}

.profile {
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    margin: 0.5rem;
}
  
.gradient-border-mask {
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 100%;
}

.gradient-border-mask::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 100%;
    background: linear-gradient(45deg, rgb(255 153 51) 20%, rgb(207 158 250) 80%);
    -webkit-mask: radial-gradient(circle, transparent 63%, black 64%);
    mask: radial-gradient(circle, transparent 63%, black 64%);
    z-index: -1;
}

.profile img {
    position: absolute;
    top: 4px; 
    left: 4px;
	right: 4px;
    bottom: 4px;
    width: 2rem;
    height: 2rem;
    object-fit: cover; 
    border-radius: 100%;
}

.site__header .title {
	font-size: 0.875rem;
	font-weight: 500;
	margin: 0;
}

.job__role {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.site__header .job__role span {
	display: none;
}

.type__tag {
	margin-bottom: 1rem;
}

.app__tag {
	display: inline-flex;
	align-items: center;
    background: transparent;
    border-radius: 2rem;
    margin: 0 .5rem .75rem 0;
    padding: .625rem 1.125rem .625rem 1rem;
    color: rgb(245 248 253);
	background-color: rgb(22 26 34);
	border: 1px solid rgb(31 41 57);
}

.app__tag span {
	font-size: 0.875rem;
	line-height: 1;
    white-space: nowrap;
	margin-left: 0.5rem;
}

.heading {
	font-weight: 400;
	font-size: 8vw;
	font-size: clamp(2rem, 10vw, 8rem);
	line-height: 1.2;
}

.heading--item {
	margin: 2rem 1rem 1rem;
}

.heading .splitting .word, 
.heading .splitting .char {
	display: flex;
}

.heading .splitting .whitespace {
    display: inline-block;
    width: 0.25em;
    height: 1.2em;
}

.char-wrap {
	display: inline-block;
	position: relative;
	overflow: hidden;
}

.char {
	display: inline-block;
	will-change: transform;
}

.heading > span {
    display: flex;
	justify-content: center;
	font-size: 8vw;
	font-size: clamp(2rem, 10vw, 8rem);
}

.heading > span:not(:first-child) {
	margin-top: -2vw;
}

.item {
	margin: 4rem 0;
}

.item__imgwrap,
.content__imgwrap {
	overflow: hidden;
    display: flex;
    align-items: center;
    width: 100%;
	max-height: calc(100vh - 4rem);
	min-height: 100%;
	will-change: transform;
}

.item__img,
.content__img {
	width: 100%;
	height: 100%;
	min-height: 120vw;
	background-size: cover;
	background-position: 50% 50%;
	will-change: transform;
}

.item__meta {
	margin: 1rem;
	line-height: 1.6;
	font-size: 1rem;
	font-weight: 500;
	text-align: center;
	overflow: hidden;
}

.item__meta .date {
	text-transform: uppercase;
	font-size: 0.6875rem;
	letter-spacing: .06em;
}

.item__meta-row {
	display: block;
	position: relative;
	overflow: hidden;
}

.item__meta-row span {
	will-change: transform;
}

.type__tag span {
	display: inline-block;
	font-size: 0.875rem;
    line-height: 1;
    margin: .25rem .5rem .25rem 0;
    padding: .5rem .75rem;
	border-radius: 2px;
	color: rgb(245 248 253);
	background-color: rgb(22 26 34);
	border: 1px solid rgb(31 41 57);
}

.item__enter {
	display: none;
	will-change: transform;
}

.item__enter-circle {
	fill: none;
	stroke: white;
	stroke-width: 1px;
	will-change: transform, opacity;
}

.item__excerpt {
	line-height: 1.2;
	text-align: center;
	will-change: transform;
	margin: 0 1rem 1rem;
}

.item__excerpt-link {
	display: inline-block;
	position: relative;
	cursor: pointer;
	color: var(--color-text);
	font-size: .875rem;
	font-weight: 500;
	line-height: 1;
	overflow: hidden;
} 

.item__excerpt-link span {
	display: inline-block;
	padding-bottom: .75rem;
	position: relative;
}

.item__excerpt-link span::after {
	content: '';
	width: 100%;
	height: 1px;
	top: calc(100% - 0.5rem);
	position: absolute;
	left: 0;
	background: white;
    opacity: .5;
    transition: opacity 0.3s ease;
}

.item__excerpt-link span:hover::after {
    opacity: 1;
}

.frame--footer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
	justify-content: space-between;
    align-items: center;
	padding: 0;
	border-top: 1px solid rgb(51 51 51 / 16%);
}

.social__links,
.email {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
}

.social, .email {
	padding: 0.5rem;
	margin: 0.5rem;
}

.email {
    font-size: 0.875rem;
    font-weight: 500;
}

.social .svg__icon,
.social .svg__icon svg {
	width: 1rem;
	height: 1rem;
	fill: white;
}

.copyright--footer {
	width: 100%;
	padding: 0.5rem 1rem;
}

.copyright {
	font-size: 0.75rem;
	line-height: 1;
    font-weight: 500;
	color: white;
	margin: 0;
}

.content {
	position: fixed;
	top: 0;
	right: 0;
	width: 100%;
	height: 100vh;
	z-index: 2000;
	pointer-events: none;
}

.content .heading {
	margin: 4rem 0;
	font-size: 10vw;
    font-size: clamp(2rem, 10vw, 8rem);
	font-weight: 400;
}

.content .heading > span {
	font-size: 10vw;
    font-size: clamp(2rem, 10vw, 8rem);
	justify-content: center;
}

.content__article {
	height: 0;
	overflow: hidden;
}

.content__article--open {
	pointer-events: auto;
	padding: 0 0 20vh;
	height: 100%;
	overflow-y: scroll;
}

.content__article .heading {
	text-align: center;
}

.content__text {
	padding: 0;
	margin: 0 auto;
	max-width: 1920px;
	will-change: transform;
}

.section__overview {
    display: grid;
    margin: 4rem 0;
    gap: 2rem;
}

.project__meta h5 {
	color: rgb(154, 160, 166);
}

.about__text h4,
.about__text p.smaller--paragraph  {
	margin-bottom: 0.25rem;
}

.about__text p,
.project__info p,
.project__meta p {
    margin-bottom: 1.5rem;
}

.tech__apps {
	margin-top: 0.75rem;
}

.content__text img {
	max-width: 100%;
	margin: 0 auto 0.5rem;
	display: block;
}

.project__info,
.project__meta {
    padding: 0 1rem;
}

.project__info {
    max-width: 800px;
    grid-column: 1;
    grid-row: 1;
}

.project__meta {
    grid-column: 1;
    grid-row: 2;
}

.project__meta h5 {
    margin-bottom: 0;
}

.project__meta ul {
    margin: 0.5rem 0 2rem;
}

.site-link {
    color: rgb(154, 160, 166);
    transition: color 0.3s;
}

.site-link::after {
    content: '\2197';
    margin-left: 0.25rem;
}

.section__column--half {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 0.5rem;
}

.section__column--third {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 0 0.5rem;
}

.section__design, 
.section__video,
.section__video--full {
	max-width: 100%;
	margin: 0 auto 0.5rem;
	aspect-ratio: auto 16 / 9;
	background-color: rgb(15, 16, 17);
}

.section__design, 
.section__video {
	display: flex;
	justify-content: center;
	align-items: center;
}

.section__design img {
    width: 100%;
    height: 100%;
	max-width: 1440px;
	max-height: 900px;
	margin: 0 auto;	
}

.section__video video {
    width: 100%;
    height: 100%;
	max-width: 1280px;
	max-height: 720px;
	margin: auto;
}

.section__video--full video {
	display: block;
	width: 100%;
	height: auto;
}

.section__column--half video,
.section__column--third video {
    width: 100%;
    height: auto;
}

.content__back {
	display: flex;
    align-items: center;
	position: absolute;
	top: 1rem;
	left: 1rem;
	opacity: 0;
	cursor: pointer;
	mix-blend-mode: difference;
    color: white;
}

.content__article--open ~ .content__back {
	pointer-events: auto;
}

.triangle--left {
    width: 6px;
    height: 8px;
    border-style: solid;
    border-width: 4px 6px 4px 0;
    border-color: transparent var(--color-text) transparent transparent;
}

.back__button {
    line-height: 2;
    font-size: 0.75rem;
    font-weight: 500;
    margin-left: 0.5rem;
	color: var(--color-text);
}

@media screen and (min-width: 53em) {
	h4 {
	font-size: 1.25rem;
	}
	h5, p, li {
		font-size: 1.125rem;
	}
	.site__header button {
		display: block;
	}
	.site__header .job__role span {
		display: inline-block;
	}
	main {
		padding: 4rem 2rem;
	}
	.item {
		position: relative;
		display: grid;
		grid-template-areas: 
		'image image image enter' 
		'meta meta meta meta' 
		'... ... excerpt ...';
		grid-template-columns: 1fr auto auto 1fr;
		grid-template-rows: minmax(420px,60vh) auto auto;
		margin: 8vh 0 calc(8vh + 4rem);
	}
	.item--invert {
		grid-template-areas: 
		'enter image image image' 
		'meta meta meta meta' 
		'... ... excerpt ...';
		grid-template-columns: 1fr auto auto 1fr;
	}
	.item__imgwrap {
		grid-area: image;
		border-radius: 0.25rem;
	}
	.item__img {
		width: 100%;
		height: 100%;
		min-height: 100%;
	}
	.item__enter {
		align-self: center;
		justify-self: center;
		display: flex;
		grid-area: enter;
		position: relative;
		z-index: 1000;
		max-width: 400px;
		max-height: 400px;
		align-items: center;
		justify-content: center;
	}
	.item__enter-circle {
		pointer-events: none;
		cursor: pointer;
		flex: none;
	}
	.item__meta {
		grid-area: meta;
		justify-self: start;
		text-align: left;
	}
	.item--invert .item__meta {
		justify-self: end;
		text-align: right;
	}
	.item__excerpt {
		grid-area: excerpt;
		margin: 0;
	}
	.item__excerpt p {
		margin: 0 0 1.5rem;
	}
	.heading--item {
		margin: 0;
		grid-area: 1 / 1 / 2 / 5;
		justify-self: center;
		align-self: center;
		text-align: center;
	}
	.content__back {
		left: 2rem;
	}
	.content__text {
		padding: 0 1rem;
	}
    .section__overview {
        grid-template-columns: 1fr 2fr 0.33fr;
        gap: 2rem;
		margin: 8vh 0 calc(8vh + 4rem);
    }
    .project__info {
        grid-column: 2;
        grid-row: 1;
    }
    .project__meta {
        grid-column: 1;
        grid-row: 1;
    }
	.content__text img,
	.section__design,
	.section__video,
	.section__video--full {
		width: 100%;
		margin: 0 auto 1rem;
		border-radius: .25rem;
	}
	.section__column--half,
	.section__column--third {
		gap: 0 1rem;
	}
	.section__video video,
	.section__video--full video,
	.section__column--half video,
	.section__column--third video {
		border-radius: .25rem;
	}
	.site-link {
		font-size: 1.125rem;
	}
	.app__tag {
		padding: .75rem 1.25rem .75rem 1.125rem;	
	}
	.type__tag span,
	.app__tag span {
		font-size: 1rem;	
	}
	
}

@media (any-pointer:fine) {
	.cursor {
		position: fixed;
		top: 0;
		left: 0;
		display: block;
		pointer-events: none;
		z-index: 5000;
	}

	.cursor__inner {
		fill: var(--cursor-fill);
		stroke: var(--cursor-stroke);
		stroke-width: var(--cursor-stroke-width);
		opacity: 0.7;
	}

	.no-js .cursor {
		display: none;
	}

}
